.col-base {
  position: relative;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
}

.col {
  composes: col-base;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.col-3 {
  composes: col-base;
  flex: 0 0 25%;
  max-width: 25%;
}

.col-4 {
  composes: col-base;
  flex: 0 0 33.33%;
  max-width: 33.33%;
}

.col-6 {
  composes: col-base;
  flex: 0 0 50%;
  max-width: 50%;
}

.col-auto {
  composes: col-base;
  flex: 0 0 auto;
  width: auto;
  max-width: 100%;
}

.left {
  margin-right: auto;
}

.right {
  margin-left: auto;
}

.center {
  margin-left: auto;
  margin-right: auto;
} 
